<template>
  <div class="record">
    <div class="recordwrapper">
      <p class="recordtitle">筛选条件</p>
      <Row v-for="(question, index) in requirements" class-name="recordrow" :key="index">
        <Col span="4" style="padding-right:10px">
          <Select @on-change='choose' placeholder='请选择题目' v-model="question.qid" filterable>
            <Option v-for="item in cityList" :value="item.qid" :key="item.qid">{{ item.topic }}</Option>
          </Select>
        </Col>
        <Col span="4" style="padding-right:10px">
          <Select placeholder='请选择逻辑' v-model="question.para" filterable>
            <Option value="1">包含</Option>
            <Option value="0">不包含</Option>
          </Select>
        </Col>
        <Col span="6">
          <Select placeholder='请选择选项(可多选)' v-model="question.okey" filterable multiple>
            <Option v-for="item in optionslist" :value="item.okey" :key="item.qid">{{ item.option }}</Option>
          </Select>
        </Col>
        <Col span="1">
          <div @click="delechoose(index)" class="iconwrapper">
             <Icon type="close"></Icon>
          </div>
        </Col>
      </Row>
      <p class="recordadd" @click="addchoose"><span>+</span>添加条件</p>
      <p class="recordtitle">显示设置</p>
      <Row class-name="recordrow">
        <Col span="8" class-name="input11">
          <Select @on-change='change' placeholder='请选择题目' v-model="model11" filterable>
            <Option v-for="item in cityList" :value="item.qid" :key="item.qid">{{ item.topic }}</Option>
          </Select>
        </Col>
<!--         <Col class-name="sharewrapper" offset="5" span="8">
          <span class="sharetext">分享</span>
          <span class="imgwrapper">
            <img src="./wechat.png">
            <img src="./qqzone.png">
            <img src="./qq.png">
            <img src="./wechatzone.png">
            <img class="smaller" src="./massage.png">
          </span>
        </Col>
 -->      </Row>
      <div style="background-color:#ccc; height:1px;">
      </div>

      <div v-if="ischange" class="questionwrapper">
        <Row>
          <Col span="12" class-name="titletext">
            题目：{{model13}}
          </Col>
          <Col offset="2" span="8" class-name="titletext">
           题目类型：{{sttype}}
         </Col>
         <Col span="2" class-name="titletext">
           {{stnum}}条数据
         </Col>
       </Row>
       <Row>
        <Col span="13" class-name="titletext">
          <div class="chartcard">
           <!--  <Row>
              <Col offset="13" span="11" class-name="choosetype">
                <span>条形图</span>
                <span>柱形图</span>
                <span>扇形图</span>
                <span><img src="./save.png"></span>
              </Col>
            </Row> -->
            <!-- <div v-show="jj" id="bar"></div>
            <div v-show="!jj" id="pie"></div> -->
            <Tabs value="name1" class="chartwrapper">
              <TabPane label="扇形图" name="name1">
                <div id="pie"></div>
              </TabPane>
              <TabPane label="条形图" name="name2">
                <div id="bar"></div>
              </TabPane>
              <TabPane label="折线图" name="name3">
                <div id="line"></div>
              </TabPane>
            </Tabs>
          </div>
        </Col>
        <Col offset="1" span="10" class-name="titletext">
          <div class="chartcard">
            <div class="tablewrapper">
              <table class="charttable">
                <thead>
                  <th class="toption">选项</th>
                  <th class="tnum">数据量</th>
                  <th class="tnum">占比</th>
                </thead>
                <tbody>
                  <tr v-for="stoption in stoptions">
                    <td class="doption">{{stoption.okey}}. {{stoption.option}}</td>
                    <td class="dnum">{{stoption.count}}</td>
                    <td class="dnum">{{stoption.proportion}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </Col>
      </Row>

    </div>
      
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import echarts from 'echarts'
export default {
  data () {
    return {
      sttype: '',
      stnum: '',
      ischange: false,
      stoptions: [],
      questionlist: [],
      qnid: '',
      requirements: [
        {qid: '', para: '', okey: []}
      ],
      charttype: 'bar',
      cityList: [],
      optionslist: [],
      questiontopics: [],
      model11: '',
      model12: [],
      model13: '',
      charts: '',
      pp: [],
      opinionData: [],
      opinion1: ['2017-07-09', '2017-07-09', '2017-07-09', '2017-07-09'],
      opinionData1: [100, 180, 110, 555],
      piedata: []
    }
  },
  watch: {
    requirements: function () {
      console.log(999)
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$http.get('statistics/qnid/' + to.params.id + '/init').then((response) => {
        // console.log(response)
        vm.$store.commit('savetopic', response.body.questionnaire)
        vm.cityList = response.body.questions
        vm.qnid = to.params.id
      })
    })
  },
  methods: {
    choose (qid) {
      this.$http.get('statistics/qid/' + qid + '/getOptions').then((response) => {
        this.optionslist = response.body.options
        // vm.$store.commit('savetopic', response.body.questionnaire)
        // vm.cityList = response.body.questions
      })
    },
    change (qid) {
      vm.$http.get('loginStatus').then(response => {
        if (response.body.status === 0) window.location.href="http://survey.twtstudio.com/api/login"
        else {
      this.$http.post('statistics/qid/' + qid + '/data', {requirements: this.requirements}).then((response) => {
        console.log(response.body)
        this.model13 = response.body.question
        this.stoptions = response.body.statistics.statistics
        this.ischange = true
        this.stnum = response.body.statistics.data.sum
        if (response.body.qtype === 0) {
          this.sttype = '单项选择'
        }
        if (response.body.qtype === 1) {
          this.sttype = '多项选择'
        }
        // console.log(response.body.statistics.statistics)
        var oldarr = response.body.statistics.statistics
        var starr = []
        var bararr = []
        var bardata = []
        for (var i = 0; i < oldarr.length; i++) {
          starr[i] = { value: oldarr[i].count, name: oldarr[i].okey }
          bararr[i] = oldarr[i].okey
          bardata[i] = oldarr[i].count
        }
        this.piedata = starr
        this.pp = bararr
        this.opinionData = bardata
        this.$nextTick(function () {
          this.drawBar('bar')
          this.drawPie('pie')
          this.drawLine('line')
        })
        // this.ischange = true
        // vm.$store.commit('savetopic', response.body.questionnaire)
        // vm.cityList = response.body.questions
      })
        }
      })
    },
    addchoose () {
      this.requirements.push({qid: '', para: '', okey: []})
    },
    delechoose (index) {
      this.requirements.splice(index, 1)
    },
    drawLine (id) {
      this.charts = echarts.init(document.getElementById(id))
      this.charts.setOption({
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.pp
        },
        yAxis: {
          splitLine: {
            show: true
          },
          offset: 100,
          interval: 200
        },
        series: [
          {
            name: '访问来源',
            type: 'line',
            data: this.opinionData,
            lineStyle: {
              normal: {
                color: '#00a1e9',
                width: 1
              }
            },
            itemStyle: {
              normal: {
                color: '#00a1e9',
                borderWidth: 4
              }
            },
            symbol: 'circle',
            symbolSize: 10,
            label: {
              normal: {
                show: true,
                color: '#707070'
              }
            }
          }
        ]
      })
    },
    drawBar (id) {
      this.charts = echarts.init(document.getElementById(id))
      this.charts.setOption({
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisLabel: {
            rotate: 90,
            interval: 0,
            color: '#707070'
          },
          axisTick: {
            length: 0
          },
          axisLine: {
            lineStyle: {
              color: '#707070'
            }
          },
          data: this.pp
        },
        yAxis: {
          splitLine: {
            show: true
          },
          offset: 100,
          interval: 150
        },
        visualMap: {
          top: 10,
          right: 10,
          showLabel: false,
          show: false,
          pieces: [
            {
              gt: 0,
              lte: 10,
              color: 'rgb(73, 202, 137)'
            },
            {
              gt: 10,
              lte: 20,
              color: 'rgb(255, 178, 62)'
            },
            {
              gt: 20,
              lte: 30,
              color: '#8d7eda'
            },
            {
              gt: 30,
              color: '#57a3f3'
            }
          ],
          outOfRange: {
            color: 'blue'
          }
        },
        series: [
          {
            name: '访问来源',
            type: 'bar',
            data: this.opinionData,
            lineStyle: {
              normal: {
                color: '#00a1e9',
                width: 1
              }
            },
            barWidth: '20px',
            symbol: 'circle',
            symbolSize: 10,
            label: {
              normal: {
                show: true,
                color: '#707070',
                position: 'top',
                rotate: 90,
                offset: [5, -10]
              }
            }
          }
        ]
      })
    },
    drawPie (id) {
      this.charts = echarts.init(document.getElementById(id))
      this.charts.setOption({
        visualMap: {
          top: 10,
          right: 10,
          showLabel: false,
          show: false,
          pieces: [
            {
              gt: 0,
              lte: 100,
              color: 'rgb(73, 202, 137)'
            },
            {
              gt: 100,
              lte: 200,
              color: 'rgb(255, 178, 62)'
            },
            {
              gt: 200,
              lte: 400,
              color: '#8d7eda'
            },
            {
              gt: 400,
              color: '#57a3f3'
            }
          ],
          outOfRange: {
            color: 'blue'
          }
        },
        series: [
          {
            name: '访问来源',
            type: 'pie',
            radius: '60%',
            center: ['50%', '50%'],
            data: this.piedata
          }
        ]
      })
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.record
  min-height 100%
  background-color rgba(233,234,238,0.52)
  position relative
  top 130px
  overflow-y auto
.recordwrapper
  max-width 1100px
  min-height 100%
  margin 0 auto
  padding-top 30px
  padding-bottom 100px
  padding-left 20px
  padding-right 20px
.recordtitle
  margin-bottom 20px
  font-size 15px
.recordrow
  margin-bottom 15px
.recordadd span
  font-size 20px
  position relative
  top 2px
.recordadd
  color #00a1e9
  font-size 13px
  cursor pointer
  margin-bottom 20px
.iconwrapper
  line-height 32px
  vertical-align middle
  font-size 15px
  color #707070
  margin-left 18px
  cursor pointer
.imgwrapper img
  width 30px
  margin-left 14px
  cursor pointer
.imgwrapper
  vertical-align middle
  position relative
  top 9px
.smaller
  width 23px !important
  position relative
  bottom 3px
.sharetext
  font-size 15px
  color #707070
.sharewrapper
  text-align right
.titletext
  margin-top 20px
.input11
  padding-right:10px
  box-sizing border-box
.chartcard
  background-color white
  border-radius 2px
  border 1px solid #dadada
  height 400px
  padding 10px
  box-sizing border-box
.choosetype
  position absolute
  top 5px
  left 95px
.choosetype span
  margin-left 8px
  cursor pointer
  box-sizing border-box
.choosetype span:hover
  color #00a1e9
.choosetype img
  width 15px
  position relative
  top 3px
  cursor pointer
#bar
  width 240px
  height 580px
  transform rotate(90deg)
  position relative
  bottom 140px
  left 190px
#pie
  width 100%
  height 330px
#line
  width 100%
  height 350px
  position relative
  bottom 30px
.chartwrapper
  height 400px
  width 100%
.tablewrapper
  max-height 380px
  overflow-y auto
  padding 8px 10px
.charttable
  width 100%
  height 100%
.charttable thead
  border-bottom 1px solid #dddee1
.charttable tr
  border-bottom 1px solid #dddee1
  height 50px
  line-height 50px
.toption
  width 50%
  text-align left
  font-size 15px
  height 30px
.tnum
  width 25%
  text-align center
  font-size 15px
.tpercent
  width 25%
  text-align center
.doption
  font-size 13px
.dnum
  text-align center
  font-size 13px
</style>



